import useQuickPriceRangeChange from '@/hooks/dlmm/useQuickPriceRangeChange'
import { Block, SelectTab } from '@cetus/design'
import useWindowWidth from '@cetus/hooks/src/useWindowWidth'
import { addComma, removeComma } from '@cetus/utils'
import { d } from '@cetusprotocol/common-sdk'
import { HStack, NumberInput, NumberInputField, Stack, Text, Tooltip, VStack } from '@chakra-ui/react'
import { useSize } from 'ahooks'
import React, { useMemo } from 'react'

const quickTabs = [
  {
    label: '19',
    value: '19'
  },
  {
    label: '49',
    value: '49'
  },
  {
    label: '79',
    value: '79'
  }
]

function QuickPriceRange({ direct = false, children }: { direct: boolean; children?: React.ReactNode[] }) {
  const {
    isMinInput,
    minValue,
    minInputRef,
    onMinInputBlur,
    onMinInputChange,
    handleMinInput,
    isMaxInput,
    maxValue,
    maxInputRef,
    onMaxInputChange,
    onMaxInputBlur,
    currentNumBins,
    handleChangeTab,
    setActiveInput,
    showMaxWarning,
    showMinWarning,
    handleMaxInput
  } = useQuickPriceRangeChange({ direct })
  const { isApp } = useWindowWidth()
  const size = useSize(document?.querySelector('.controlPriceRange'))
  const appH = useMemo(() => {
    return d(size?.height).sub(8).div(2).toNumber()
  }, [size?.height])
  return (
    <Stack flexDir={{ base: 'column', lg: 'row' }} w="100%" gap="8px" h={{ base: 'auto', lg: '32px' }} justify="space-between">
      {isApp ? (
        <>
          <HStack>
            {children?.[0]}
            <VStack flex="0 0 100px">
              <Block
                flex={{ base: `0 0 ${appH}px`, lg: '0 0 212px' }}
                h="100%"
                border="1px solid"
                borderColor={isMinInput ? 'token_active_border' : 'border'}
                boxShadow={isMinInput ? '0px 0px 8px 0px #0067AD' : ''}
                display="flex"
                p="6px"
                borderRadius="12px"
                justifyContent="center"
                alignItems="center"
              >
                <Tooltip
                  label={
                    <Text fontSize="12px" lineHeight="20px">
                      Percentage Min: -99.99%
                    </Text>
                  }
                  isOpen={showMinWarning}
                  placement="top"
                >
                  <NumberInput
                    value={minValue}
                    precision={2}
                    // min={-99.99}
                    inputMode="numeric"
                    clampValueOnBlur
                    variant="unstyled"
                    format={value => (value ? addComma(value) + ' %' : '')}
                    onFocus={() => {
                      setActiveInput('min')
                    }}
                    onBlur={e => onMinInputBlur(removeComma(e.target.value))}
                    onChange={(valueString, valueNumber) => onMinInputChange(removeComma(valueString), valueNumber)}
                    color={isMinInput ? 'text_caption' : 'text_paragraph'}
                    w="100%"
                  >
                    <NumberInputField
                      fontSize="14px"
                      color="text_caption"
                      paddingInlineStart="4px"
                      paddingInlineEnd="4px"
                      textAlign="center"
                      ref={minInputRef}
                      onInput={handleMinInput}
                      onKeyDown={handleMinInput}
                      onMouseUp={handleMinInput}
                    />
                  </NumberInput>
                </Tooltip>
              </Block>
              <Block
                flex={{ base: `0 0 ${appH}px`, lg: '0 0 212px' }}
                h="100%"
                border="1px solid"
                borderColor={isMaxInput ? 'token_active_border' : 'border'}
                boxShadow={isMaxInput ? '0px 0px 6px 0px #0067AD' : ''}
                display="flex"
                p="6px"
                borderRadius="12px"
                justifyContent="center"
                alignItems="center"
              >
                <Tooltip
                  label={
                    <Text fontSize="12px" lineHeight="20px">
                      Percentage Min: -99.99%
                    </Text>
                  }
                  isOpen={showMaxWarning}
                  placement="top"
                >
                  <NumberInput
                    value={maxValue}
                    // min={-99.99}
                    precision={2}
                    variant="unstyled"
                    format={value => (value ? addComma(value) + ' %' : '')}
                    clampValueOnBlur
                    onFocus={() => {
                      setActiveInput('max')
                    }}
                    onChange={(valueString, valueNumber) => onMaxInputChange(removeComma(valueString), valueNumber)}
                    onBlur={e => onMaxInputBlur(removeComma(e.target.value))}
                    color={isMaxInput ? 'text_caption' : 'text_paragraph'}
                    w="100%"
                  >
                    <NumberInputField
                      fontSize="14px"
                      color="text_caption"
                      paddingInlineStart="4px"
                      paddingInlineEnd="4px"
                      textAlign="center"
                      ref={maxInputRef}
                      onInput={handleMaxInput}
                      onKeyDown={handleMaxInput}
                      onMouseUp={handleMaxInput}
                    />
                  </NumberInput>
                </Tooltip>
              </Block>
            </VStack>
          </HStack>
          {children?.[1]}
        </>
      ) : (
        <>
          <Block
            flex={{ base: '0 0 32px', lg: '1' }}
            h="100%"
            border="1px solid"
            borderColor={isMinInput ? 'token_active_border' : 'border'}
            boxShadow={isMinInput ? '0px 0px 8px 0px #0067AD' : ''}
            display="flex"
            p="6px"
            borderRadius="12px"
            justifyContent="center"
            alignItems="center"
          >
            <Tooltip
              label={
                <Text fontSize="12px" lineHeight="20px">
                  Percentage Min: -99.99%
                </Text>
              }
              isOpen={showMinWarning}
              placement="top"
            >
              <NumberInput
                value={minValue}
                precision={2}
                // min={-99.99}
                inputMode="numeric"
                clampValueOnBlur
                variant="unstyled"
                format={value => (value ? addComma(value) + ' %' : '')}
                onFocus={() => {
                  setActiveInput('min')
                }}
                onBlur={e => onMinInputBlur(removeComma(e.target.value))}
                onChange={(valueString, valueNumber) => onMinInputChange(removeComma(valueString), valueNumber)}
                color={isMinInput ? 'text_caption' : 'text_paragraph'}
                w="100%"
              >
                <NumberInputField
                  fontSize="14px"
                  color="text_caption"
                  paddingInlineStart="4px"
                  paddingInlineEnd="4px"
                  textAlign="center"
                  ref={minInputRef}
                  onInput={handleMinInput}
                  onKeyDown={handleMinInput}
                  onMouseUp={handleMinInput}
                />
              </NumberInput>
            </Tooltip>
          </Block>
          <Block
            flex={{ base: '0 0 32px', lg: '1' }}
            h="100%"
            border="1px solid"
            borderColor={isMaxInput ? 'token_active_border' : 'border'}
            boxShadow={isMaxInput ? '0px 0px 6px 0px #0067AD' : ''}
            display="flex"
            p="6px"
            borderRadius="12px"
            justifyContent="center"
            alignItems="center"
          >
            <Tooltip
              label={
                <Text fontSize="12px" lineHeight="20px">
                  Percentage Min: -99.99%
                </Text>
              }
              isOpen={showMaxWarning}
              placement="top"
            >
              <NumberInput
                value={maxValue}
                precision={2}
                // min={-99.99}
                variant="unstyled"
                format={value => (value ? addComma(value) + ' %' : '')}
                clampValueOnBlur
                onFocus={() => {
                  setActiveInput('max')
                }}
                onChange={(valueString, valueNumber) => onMaxInputChange(removeComma(valueString), valueNumber)}
                onBlur={e => onMaxInputBlur(removeComma(e.target.value))}
                color={isMaxInput ? 'text_caption' : 'text_paragraph'}
                w="100%"
              >
                <NumberInputField
                  fontSize="14px"
                  color="text_caption"
                  paddingInlineStart="4px"
                  paddingInlineEnd="4px"
                  textAlign="center"
                  ref={maxInputRef}
                  onInput={handleMaxInput}
                  onKeyDown={handleMaxInput}
                  onMouseUp={handleMaxInput}
                />
              </NumberInput>
            </Tooltip>
          </Block>
        </>
      )}

      <SelectTab<any, string>
        tabList={quickTabs}
        currentTab={currentNumBins}
        handleChangeTab={handleChangeTab}
        type="outlineTab"
        wrapStyle={{ flex: { base: '0 0 32px', lg: '0 0 160px' }, h: '32px', gap: 0, p: '3px', borderRadius: '12px' }}
        itemStyle={{
          flex: 1,
          borderRadius: '8px'
        }}
      />
    </Stack>
  )
}

export default QuickPriceRange
