import Slippage from '@/components/common/Slippage'
import TotalAmount from '@/components/common/TotalAmount'
import useDlmmLiquidityStore from '@/store/dlmm'
import useAddDlmmLiquidityStore from '@/store/dlmm/addDlmmLiquidity'
import { Block, ErrorTips, TradeInputGroup } from '@cetus/design'
import useTokenPrice from '@cetus/hooks/src/useTokenPrice'
import { Box, Button, HStack, Text, VStack } from '@chakra-ui/react'
import AutoFillSwitch from '../AutoFillSwitch'
import SelectStrategy from '../SelectStrategy'
import SetPriceRange from '../SetPriceRange'
import { DLMMDepositProps } from './type'

function H5Deposit({
  btnText,
  btnDisabled,
  onReverseClick,
  direct,
  perText,
  rangeTabList,
  currentRangeTab,
  handleAmountChange,
  fromAmountValue,
  toAmountValue,
  fromBalanceInfo,
  toBalanceInfo,
  handleAdd,
  submitLoading,
  isReverse
}: DLMMDepositProps<any>) {
  const { dlmmApiPoolInfo } = useDlmmLiquidityStore()
  const {
    fromToken,
    toToken,
    fromAmount,
    toAmount,
    byAmountIn,
    totalAmount,
    fromTokenLock,
    toTokenLock,
    minPriceData,
    maxPriceData,
    positionCount,
    numBins,
    fromLoading,
    toLoading,
    preCalcError
  } = useAddDlmmLiquidityStore()

  const { getTokenAmountValue } = useTokenPrice()

  return (
    <VStack w="100%">
      <VStack w="100%" borderRadius="12px" border="1px solid" borderColor="border" bg="card_bg" p="12px" gap="16px">
        <HStack w="100%" h="32px" justify="space-between">
          <Text lineHeight="20px" fontSize="16px" fontWeight="500" color="text_caption">
            Deposit Amounts
          </Text>
          <HStack>
            <Block p="4px 4px 4px 8px" borderRadius="8px">
              <AutoFillSwitch disabled={fromTokenLock || toTokenLock} />
            </Block>
            <Slippage slippageType="liquidity" poolType="dlmm" />
          </HStack>
        </HStack>
        <Box w="100%" position="relative">
          <TradeInputGroup
            onClick={onReverseClick}
            from={{
              wrapStyle: { h: '108px' },
              balance: fromBalanceInfo?.balanceFormat || '',
              value: fromAmount,
              amountValue: getTokenAmountValue(fromToken?.coin_type, fromAmount),
              loading: false,
              onChange: value => {
                handleAmountChange(value, true, fromToken?.coin_type === dlmmApiPoolInfo?.coin_type_a)
              },
              selectable: false,
              placeholder: '0.0',
              token: fromToken,
              remainBalanceAmount: 0.5,
              onFocusChange: (focus: boolean) => {
                if (focus && +(fromAmount + '') && !byAmountIn) {
                  handleAmountChange(fromAmount + '', true, fromToken?.coin_type === dlmmApiPoolInfo?.coin_type_b)
                }
              },
              lock: {
                isLock: fromTokenLock && !toTokenLock,
                text: 'The current price is outside your specified price range. Single-asset deposit only.'
              },
              balancePosition: 'bottom',
              rightJustify: 'space-around'
            }}
            to={{
              wrapStyle: { h: '108px' },
              balance: toBalanceInfo?.balanceFormat || '',
              value: toAmount,
              amountValue: getTokenAmountValue(toToken?.coin_type, toAmount),
              loading: false,
              remainBalanceAmount: 0.5,
              onFocusChange: (focus: boolean) => {
                if (focus && +(toAmount + '') && byAmountIn) {
                  handleAmountChange(toAmount + '', false, toToken?.coin_type === dlmmApiPoolInfo?.coin_type_a)
                }
              },
              onChange: value => {
                handleAmountChange(value, false, toToken?.coin_type === dlmmApiPoolInfo?.coin_type_b)
              },
              selectable: false,
              placeholder: '0.0',
              token: toToken,
              lock: {
                isLock: !fromTokenLock && toTokenLock,
                text: 'The current price is outside your specified price range. Single-asset deposit only.'
              },
              balancePosition: 'bottom',
              rightJustify: 'space-around'
            }}
            iconParams={{
              xlinkHref: '#icon-icon_add',
              svgFill: 'text_caption'
            }}
            lock={{
              isLock: fromTokenLock && toTokenLock,
              style: { h: '224px' }
            }}
          />
        </Box>
        <TotalAmount totalAmount={totalAmount} loading={false} valueStyle={{ h: '20px', lineHeight: '20px' }} />
      </VStack>
      <VStack w="100%" borderRadius="12px" border="1px solid" borderColor="border" bg="card_bg" p="16px 12px 12px" gap="16px" align="flex-start">
        <Text lineHeight="20px" fontSize="16px" fontWeight="500" color="text_caption">
          Select Strategy
        </Text>
        <SelectStrategy />
      </VStack>
      <VStack w="100%" borderRadius="12px" border="1px solid" borderColor="border" bg="card_bg" p="12px" gap="0px">
        <SetPriceRange
          perText={perText}
          direct={direct !== isReverse}
          minPriceData={minPriceData}
          maxPriceData={maxPriceData}
          rangeTabList={rangeTabList}
          currentRangeTab={currentRangeTab}
          onReverseClick={onReverseClick}
        />
        <VStack w="100%" gap="8px" className="dlmm-tutorial-step-6">
          {!!dlmmApiPoolInfo?.id && !(fromTokenLock && toTokenLock) && (
            <VStack gap="12px" w="100%" borderRadius="16px">
              {preCalcError && (
                <ErrorTips
                  tipsFontSize="12px"
                  tipsLineHeight="12px"
                  tips={'Amount too small to allocate across bins. Please increase the amount.'}
                  type="warning"
                />
              )}

              <Button
                w="100%"
                h="52px"
                margin="-1px -1px 5px"
                fontSize="18px"
                fontWeight="500"
                borderRadius="12px"
                isDisabled={btnDisabled}
                isLoading={submitLoading}
                onClick={handleAdd}
              >
                {btnText}
              </Button>
            </VStack>
          )}
        </VStack>
      </VStack>
    </VStack>
  )
}

export default H5Deposit
